.upcoming-event-card__datetime {
  display: block;
  color: #005d51;
  font-size: 16px; }
  @media screen and (min-width: 48em) {
    .upcoming-event-card__datetime {
      font-size: 18px; } }

.upcoming-event-card__title {
  display: block;
  font-size: 16px;
  line-height: 1;
  color: #fefefe; }
  @media screen and (min-width: 48em) {
    .upcoming-event-card__title {
      font-size: 18px; } }
  .upcoming-event-card__title:hover, .upcoming-event-card__title:focus {
    color: #a7e7df; }
